<template>
  <div class="container">
      <h2>Page Two - 모달 띄우기 </h2>
      <ul class="test-list">
        <List v-bind:temp="temp" v-for="(temp,index) in temps" v-bind:key="index" v-on:click.native="openModal"/>
      </ul>

    <transition name="bboing">
      <Modal v-if="showing"
        v-on:showing="showing=$event" v-bind:showing="showing"
        v-bind:temp="temps" v-bind:clickNum="clickNum"/>
    </transition>
  </div>
</template>

<script>

import Temps from '@/store/temps.js'
import Modal from '@/components/Modal.vue'
import List from '@/components/List.vue'

  export default{
    name:"pagetwo",
    components:{
      Modal,List
    },
    data(){
      return{
        temps:Temps,
        showing:false,
        clickNum:0
      }
    },
    methods:{
      openModal(e){
        this.showing = true;
        this.clickNum = e.target.dataset.id;
      },
    }
    ,
    props:{
      // temp:Object
    },
  }
</script>

